* {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}

ul {
  box-sizing: border-box;
  padding: 0 1em;
}

#grade-list {
  display: flex;
  flex-direction: column;
  gap: 5px;
  width: 375px;
}

.subject-div {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}
.subject-div label {
  width: 65%;
}
.subject-div input {
  width: 30%;
  padding: 2px;
}

#god-father {
  display: flex;
  width: 100%;
  justify-content: center;
}

#actual-father {
  width: 375px;
  display: flex;
  flex-direction: column;
  gap: 10px;
}

@media (max-width: 767px) {
  #actual-father {
    width: 280px;
  }
  #grade-list {
    width: 280px;
  }
}